import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{title:'ChefLancer - Home'}
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
    meta:{title:'About - ChefLancer'}
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue'),
    meta:{title:'Login - ChefLancer'}
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "about" */ '../views/Register.vue'),
    meta:{title:'Register - ChefLancer'}
  },
  {
    path: '/controlpanel/:uid',
    name: 'ControlPanel',
    component: () => import(/* webpackChunkName: "about" */ '../views/ControlPanel.vue'),
    meta:{title:'ControlPanel - ChefLancer'}
  },
  {
    path: '/chefs',
    name: 'Chefs',
    component: () => import(/* webpackChunkName: "about" */ '../views/Chefs.vue'),
    meta:{title:'Chefs - ChefLancer'}
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import(/* webpackChunkName: "about" */ '../views/ContactUs.vue'),
    meta:{title:'Contact - ChefLancer'}
  },
  {
    path: '/profile/:uid',
    name: 'Profile',
    component: () => import(/* webpackChunkName: "about" */ '../views/Profile.vue'),
    meta:{title:'Profile - ChefLancer'}
  }
]

const router = new VueRouter({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    document.getElementById('app').scrollIntoView();
  },
  base: process.env.BASE_URL,
  routes
})

export default router
