<template>
  <v-app>
    <div class="" id="top"></div>
    <!-- navbar -->
    <v-app-bar
      app
      :color="navcolor"
      :hide-on-scroll="false"
      dark
      sticky
      class="app-bar"
      
    >
      <v-spacer v-show="isSmallScreen" ></v-spacer>
      <img class="navlogoimg" src="./assets/icons/logo.png" alt="">
      <span class="logo-nav" >ChefLancer</span>
      <v-spacer></v-spacer>

      <div class="topNavLinksContainer" v-show="!isSmallScreen">
        <div class="valign-wrapper"><router-link to="/"         ><div class= "NavLinkContainer">Home</div></router-link></div>
        <div class="valign-wrapper"><router-link to="/about"    ><div class= "NavLinkContainer">About</div></router-link></div>
        <div class="valign-wrapper"><router-link to="/chefs"     ><div class= "NavLinkContainer">Chefs</div></router-link></div>
        <div class="valign-wrapper"><router-link to="/contact"  ><div class= "NavLinkContainer">Contact Us</div></router-link></div>
        <div class="valign-wrapper"><router-link to="/login"    ><div class= "NavLinkContainer SpecialNavLink">Login</div></router-link></div>
      </div>                
      
      <div class="menu-icon" @click="menuBtnClick()" v-show="isSmallScreen">
          <v-icon>mdi-menu</v-icon>
      </div>
    </v-app-bar>
    
    <!-- ovelay-menu -->
    
    <div class="navigation-overlay">

     <v-container>
       
      <v-row
        justify="center" align="center"
      >
        <v-col cols="12" v-on:click="menuBtnClick()"><router-link to="/"        ><div class="OverlayLinkContainer">Home</div>                                       </router-link></v-col>
        <v-col cols="12" v-on:click="menuBtnClick()"><router-link to="/about"   ><div class="OverlayLinkContainer">About</div>                                      </router-link></v-col>
        <v-col cols="12" v-on:click="menuBtnClick()"><router-link to="/chefs"    ><div class="OverlayLinkContainer">Chefs</div>                                       </router-link></v-col>
        <v-col cols="12" v-on:click="menuBtnClick()"><router-link to="/contact" ><div class="OverlayLinkContainer">Contact Us</div>                                 </router-link></v-col>
        <v-col cols="12" v-on:click="menuBtnClick()"><router-link to="/login"   ><div class="OverlayLinkContainer"><div class="SpecialOverlayLink">Login</div></div></router-link></v-col>
      </v-row>
       
     </v-container>
    </div>
    
    <!-- content -->
    <router-view></router-view>
    <!-- Footer -->
    <PageFooter/>


  </v-app>
</template>

<script>


import M from 'materialize-css';
import $ from 'jquery';

import PageFooter  from './components/PageFooter'

export default {
  name: 'App',

  components: {
    PageFooter,

  },

  data: () => ({
    navcolor: '#161217',
    screenWidth: 0,
    isSmallScreen:false,
    scrollPosition:0,
    menuIconText:'mdi-add',
    menuOpen: false,
    overlayColor: "#303960",
  }),
  methods:{
    width: function(){
      return window.innerWidth;
    },
    resizeEventHandler: function(){
      this.screenWidth = window.innerWidth;
      if( this.screenWidth < 760 ) this.isSmallScreen = true;
      else this.isSmallScreen = false;
      this.menuOpen = false;
      this.menuFunc();
    },
    scrollEventHandler: function(){
      this.setNavColor();
      this.menuOpen = false;
      this.menuFunc();
    },
    menuBtnClick: function(){
      this.menuOpen = ! this.menuOpen;
      this.menuFunc();
    },
    menuFunc: function(){
      if(this.menuOpen){
        this.navcolor="#3D314A";
        $('.navigation-overlay').css('zIndex','4');
        $('.navigation-overlay').css('opacity','1');
        var delay = 0.2;
        $('.OverlayLinkContainer').each(function(){
          $(this).css('animation',`0.5s revealLinks forwards ${delay}s`);
          delay += 0.2;
        });
        
      }else{
        this.setNavColor();
        $('.OverlayLinkContainer').css('animation',``);
        $('.navigation-overlay').css('zIndex','-1');
        $('.navigation-overlay').css('opacity','0');

      }
    },
    setNavColor: function(){
      this.scrollPosition = window.scrollY;
      if( this.scrollPosition > 50 ) this.navcolor = "#161217";
      else this.navcolor = "#1612171a";
    }
  },
  conputed:{
    
  },created() {
    window.addEventListener("resize", this.resizeEventHandler);
    window.addEventListener("scroll", this.scrollEventHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeEventHandler);
    window.removeEventListener("scroll", this.scrollEventHandler);
  },
  mounted(){
    M.AutoInit();
    this.resizeEventHandler();
    this.scrollEventHandler();
  }
};
</script>

<style>
