  z-index:-1;
  transition: 0.2s all;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.OverlayLinkContainer{
  color:black;
  text-align: center;
  font-size:1.5rem;
  padding:10px;
  margin: 5px;
  opacity:0;
  transition: all 0.2s;
}

.OverlayLinkContainer:hover{
  color:#3D314A;
  font-size: 2.5rem;
  letter-spacing: 10px;
}

.SpecialOverlayLink{
  color:black;
  border: 5px solid black;
  padding:15px;
  border-radius: 50px ;
  transition: all 0.2s;
}

.SpecialOverlayLink:hover{
  background: #3D314A;
  color:white;
  border: 5px solid #3D314A;
  font-size: 2.5rem;
  letter-spacing: 10px;
  border-radius: 5px 75px 5px 75px;
  animation: 0.7s SpecialOverlayLinkAnim infinite alternate 0.2s;
}
@keyframes SpecialOverlayLinkAnim{
  from{
    border-radius: 5px 75px 5px 75px;
  }
  to{
    border-radius: 75px 5px 75px 5px;
  }
}

@keyframes revealLinks {
  from{
    opacity:0;
    transform: translateY(0px);
  }
  to{
    opacity:1;
    transform: translateY(-30px);
  }
}

.topNavLinksContainer{
  display:flex;
  justify-content: space-around;
}

.NavLinkContainer{
  background: transparent;
  padding: 5px 15px;
  font-size: 1.1rem;
  transition: all 0.2s;
  border-radius: 15px ;
  color:white;
}
.NavLinkContainer:hover{
  font-size: 1.5rem;
  color:rgb(152, 13, 36);
}
.SpecialNavLink{
  color:white;
  border: 2px solid white;
}
.SpecialNavLink:hover{
  background: rgb(152, 13, 36);
  color:white;
  border: 2px solid #3D314A;
  animation: 0.5s SpecialNavLinkAnim ease-in infinite alternate;
}

@keyframes SpecialNavLinkAnim{
  from{
    border-radius: 2px 30px 2px 30px;
  }
  to{
    border-radius: 30px 2px 30px 2px;
  }
}

.router-link-exact-active .NavLinkContainer, .router-link-exact-active .NavLinkContainer:hover{
  border-radius: 15px ;
  background:#3D314A;
  color:white;
}

.router-link-exact-active .OverlayLinkContainer{
  border-radius: 15px ;
  font-weight: 700;
  letter-spacing: 10px;
  text-transform: uppercase;
  color:#3D314A;
}
.router-link-exact-active .OverlayLinkContainer:hover{
  font-size: 2rem;
  letter-spacing: 5px;
}

.v-content{
  top:0 !important;
  left:0 !important;
